import React, { Component } from "react";
import { SideBySideMagnifier } from "react-image-magnifiers";

import ImageContainer from "./container";
import './image.css';
import { Link } from 'react-router-dom';

class ProductImage extends Component {

    state = {
        allowOverflow: true,
        magnifierBorderSize: 2,
        magnifierBorderColor: "rgba(255, 255, 255, .5)",
        magnifierSize: "5%",
        square: false,
        srcimages: [],
        currentImage: ''
    };

    componentDidMount() {
        const { product, srcImages, currentImage, variations } = this.props;

        if (!product.doesContainVar) {
            this.props.defaultVar('default');
            this.setState((prevState, props) => {
                return {
                    srcimages: srcImages,
                    currentImage: currentImage
                }
            });
        } else {
            this.props.defaultVar(variations[0].label);
            if (variations[0].doesVarContainImages) {
                this.setState((prevState, props) => {
                    return {
                        srcimages: variations[0].images,
                        currentImage: variations[0].images[0]
                    }
                });
            } else {
                this.setState((prevState, props) => {
                    return {
                        srcimages: srcImages,
                        currentImage: currentImage
                    }
                });
            }
        }
    }


    onImageClick = (e) => {
        this.setState((prevState, props) => {
            return { currentImage: [e.target.getAttribute('src')] }
        });
    }

    onVariationClick = (e) => {
        this.props.hpv(e)
        const { product, srcImages, currentImage, variations } = this.props;
        console.log('I was triggered during componentDidMount' + e.target.value)
        /* get all images for e.target.value*/
        {
            variations.forEach(variation => {
                if (variation.label === e.target.value) {
                    if (variation.doesVarContainImages) {
                        console.log('changing src images' + e.target.value)
                        this.setState((prevState, props) => {
                            return {
                                srcimages: srcImages,
                                currentImage: srcImages[0]
                            }
                        });
                    } else {
                        this.setState((prevState, props) => {
                            return {
                                srcimages: srcImages,
                                currentImage: currentImage
                            }
                        });
                    }
                }

            })
        }
    }

    handleSize = e => {
        const value = e.target.value + "%";
        this.setState(() => ({ magnifierSize: value }));
    };

    handleBorderSize = e => {
        const value = Number(e.target.value);
        this.setState(() => ({ magnifierBorderSize: value }));
    };

    handleBoolChange = key => e => {
        const value = Boolean(e.target.value);
        this.setState(() => ({ [key]: value }));
    };

    handleTextChange = key => e => {
        const value = e.target.value;
        this.setState(() => ({ [key]: value }));
    };

    render() {
        const {
            allowOverflow,
            magnifierSize,
            magnifierBorderSize,
            magnifierBorderColor,
            square,
            srcimages,
            currentImage
        } = this.state;

        const { product, variations } = this.props;
        const imageDiv =
            <div style={{ paddingBottom: "10px" }} className="image-app">
                <ImageContainer title="Image Container">
                    <div className="flex">
                        <SideBySideMagnifier
                            className="glass-magnifier"
                            imageSrc={currentImage}
                            largeImageSrc={currentImage}
                            allowOverflow={allowOverflow}
                            magnifierSize={magnifierSize}
                            magnifierBorderSize={magnifierBorderSize}
                            magnifierBorderColor={magnifierBorderColor}
                            square={square}
                        />
                    </div>
                </ImageContainer>
            </div>;

        const allimages = srcimages.map((image) =>
            <img onClick={this.onImageClick} src={image} alt="p1"></img>
        );


        return (
            <React.Fragment>
                <div>
                    <div className="all-images">
                        {allimages}
                    </div>
                    {imageDiv}
                </div>
            </React.Fragment>
        );

    }
}


export default ProductImage;
