import React, { useReducer, useRef, useEffect } from 'react';
import { useFetch, useLazyLoading } from './hooks'

import './index.css';
import './productinfo.css';
import './productdetails.css';
import ProductImage from './components/image';
import { Container } from './components/container';
import { SocialMedia } from './components/media'
import { Helmet } from "react-helmet";

import Frame from './components/market';

const Product2 = (props) => {


    const imgReducer = (state, action) => {
        switch (action.type) {
            case 'STACK_IMAGES':
                return { ...state, status: action.response.status }
            case 'FETCHING_IMAGES':
                return { ...state, fetching: action.fetching }
            default:
                return state;
        }
    }

    const [productData, imgDispatch] = useReducer(imgReducer, {
        allowOverflow: true,
        magnifierBorderSize: 2,
        magnifierBorderColor: "rgba(255, 255, 255, .5)",
        magnifierSize: "5%",
        numOfImages: 0,
        url: process.env.REACT_APP_productUrl, pid: props.match.params.id,
        amazonUrl: process.env.REACT_APP_amazonUrl,
        flipkartUrl: process.env.REACT_APP_flipkartUrl,
        contactNo: process.env.REACT_APP_contactNo,
        amazonAsin: '',
        flipkartPid: '',
        square: false, fetching: true, product: {}, status: '', keyFeatures: [], variations: [], numOfImages: 0, images: [], selectedVariation: '', pid: props.match.params.id
    })

    useFetch(productData, imgDispatch);
    useLazyLoading('.card-img-top', productData.images)


    const featureList = productData.keyFeatures.map((feature, i) =>
        <p class="font-weight-bold">
            <li class="list-group-item" key={feature.key}>
                {feature}
            </li>
        </p>
    );


    const setDefaultVariation = (variation) => {
        productData.selectedVariation = variation;
    }

    const handleProductVariationSelection = (e) => {
        productData.selectedVariation = e.target.value;
    }

    const amazonLink = productData.amazonUrl + productData.amazonAsin;
    const flipkartLink = productData.flipkartUrl + productData.flipkartPid;

    const saving = parseInt(productData.product.mrp) - parseInt(productData.product.salePrice);
    const savingPer = ((saving / productData.product.mrp) * 100).toFixed(2);

    const srcImg = '/catalog/' + productData.pid + '/01.jpg';
    const srcImgSmall = '/catalog/' + productData.pid + '/01-small.jpg';
    var i;
    if (productData.images.length == 0) {
        for (i = 0; i <= productData.numOfImages; i++) {
            if (i > 0)
                productData.images.push('/catalog/' + productData.pid + '/0' + i + '.jpg');
        }
    }

    const productDetails = <table class="table table-hover">
        <tr>
            <td>Brand</td>
            <td>{productData.product.brandName}</td>
        </tr>
        <tr>
            <td>Manufacturer</td>
            <td>{productData.product.manufacturer}</td>
        </tr>
        <tr>
            <td>Country Of Origin</td>
            <td>{productData.product.countryOfOrigin}</td>
        </tr>
        <tr>
            <td>Product Category</td>
            <td>{productData.product.productCategory}</td>
        </tr>
    </table>;

    const triggerText = 'Buy Now';
    const notiftText = 'Notify Me When Available.';
    const encodeUrl = process.env.REACT_APP_vlocalShopUrl + 'product/' + productData.pid;
    const ecodedUrl = encodeURIComponent(encodeUrl);
    const whatsapplink = process.env.REACT_APP_whatsAppUrl + '?text=' + ecodedUrl;
    //console.log(whatsapplink);
    const ogImg = process.env.REACT_APP_vlocalShopUrl + '/catalog/' + productData.pid + '/01-small.jpg';
    return (

        <div className="product">
            <Helmet>
                <title>{productData.product.Title}</title>
                <meta property="og:image" itemprop="image" content={ogImg}></meta>
                <meta property="og:title" content={productData.product.Title}></meta>
            </Helmet>
            {productData.status != 'success' && (
                <div className="productWrapper">
                    <h3 className="m-0 text-white">No Such Product Available...</h3>
                </div>
            )}

            {productData.status == 'success' && (
                <div >
                    <div className="productWrapper">
                        <div className="productImage">
                            <ProductImage
                                product={productData.product}
                                variations={productData.variations}
                                srcImages={productData.images}
                                currentImage={srcImg}
                                hpv={handleProductVariationSelection}
                                defaultVar={setDefaultVariation}
                            />

                        </div>
                        <div className="productInfo">
                            <p class="h4">{productData.product.Title}</p>
                            <p class="font-weight-bold text-primary">Brand : {productData.product.brandName}</p>
                            <div className="stars">
                                <span style={{ color: "yellow" }}>
                                    <i class="fas fa-star"></i>
                                    <i class="fas fa-star"></i>
                                    <i class="fas fa-star"></i>
                                    <i class="fas fa-star"></i>
                                    <i class="fas fa-star-half-alt"></i>
                                </span>
                            </div>
                            <div className="productPrice">
                                <div id="product-label">MRP:<div id="product-label-value">₹ {productData.product.mrp}</div></div>
                                <div id="product-label">Price:<div id="product-label-value">₹ {productData.product.salePrice}</div></div>
                                <div className="buyBox">
                                    <Container
                                        triggerText={triggerText}
                                        productId={productData.product.productId}
                                        productImg={srcImg}
                                        productTitle={productData.product.Title}
                                        productPrice={productData.product.salePrice} />
                                </div>
                            </div>

                            <div className="productPrice">
                                <div id="product-label">Your Savings : <div id="product-label-value">₹ {saving} ({savingPer}%)</div></div>
                                <div id="product-label">FREE DELIVERY : <div id="product-label-value">₹ 0</div></div>
                            </div>
                            <div className="productPrice">
                                {productData.product.stock == '0' &&
                                    <div>
                                        <div id="product-label-stock">Out Of Stock : </div>
                                        <Container
                                            triggerText={notiftText}
                                            productId={productData.product.productId}
                                            productImg={srcImg}
                                            productTitle={productData.product.Title}
                                            productPrice={productData.product.salePrice} />
                                        <div id="product-label-stock"><a href={productData.product.SEO} target="_blank"> Product Selection Guide</a></div>
                                    </div>
                                }
                                {productData.product.stock != '0' &&
                                    <div id="product-label-stock">Only {productData.product.stock} left in stock.
                        			<a href={productData.product.SEO} target="_blank"> Product Selection Guide </a>
                                    </div>
                                }
                                <div className="buyBox">
                                    <a href={whatsapplink} target="_blank">
                                        <img src="/img/small-whatsapp.jpg" alt="WhatsApp" />
                                    </a>
                                </div>
                            </div>
                            <div className="social-media">
                                <SocialMedia productLink={ecodedUrl} whatsapplink={whatsapplink} />
                            </div>
                            <div className="productFeatures">
                                <p class="h5">Product Features:</p>
                                <ul class="list-group">
                                    {featureList}
                                </ul>

                            </div>
                        </div>
                    </div>
                    <div className="market">
                        <Frame
                            amazonLink={amazonLink}
                            amazonPrice={productData.product.marketplace.amazonPrice}
                            flipkartLink={flipkartLink}
                            flipkartPrice={productData.product.marketplace.flipkartPrice}
                            title={productData.product.Title}
                            vlocalShopPrice={productData.product.salePrice}
                            srcImgSmall={srcImgSmall} />
                    </div>
                    <p class="h4">Product Details:</p>
                    <p class="font-weight-normal">{productDetails}</p>
                    <p class="h4">Product Description:</p>
                    <p class="font-weight-normal">{productData.product.productDescription}</p>
                </div>
            )}


        </div>
    );
}

export default Product2;