import React, { useState, useEffect } from "react";

export default function AffliateInformation(props) {
    const [data, setData] = useState([]);
    const [invalidLSA, setInvalidLSA] = useState('');
    const [loading, setLoading] = useState(false);
    const [sucess, setSucess] = useState(true);
    const [payable, setPayable] = useState(0);

    const lsaid = props.match.params.id;

    useEffect(() => {
        setLoading(true);
        // fetch("/api/data").then(
        //     res => setState(res.data)
        // )
        setData(mockResServerProducts);
        setPayable(mockResServerBal);
        console.log(data)
        setLoading(false);
        setSucess(true);
    }, []);
    const mockResServerBal = "₹ 249.9";
    const mockResServerProducts =
        [{
            index: 0,
            orderId: 1426,
            pid: "ZN9GFLD7PU",
            noOfClicks: 0,
            orderValue: 1999,
            commission: 99.45,
            date: "13-Feb-2021"
        }, {
            index: 2,
            orderId: 1426,
            pid: "ZN9GFLD7PU",
            noOfClicks: 0,
            orderValue: 1999,
            commission: 99.45,
            date: "13-Feb-2021"
        }];

    const featureList = data.map((product) =>
        <tr>
            <th scope="row">{product.index}</th>
            <td>{product.orderId}</td>
            <td>{product.pid}</td>
            <td>{product.orderValue}</td>
            <td>{product.commission}</td>
            <td>{product.date}</td>
        </tr>
    );

    const productList = data.map((product) =>
        <tr>
            <th scope="row">{product.index}</th>
            <td>{product.pid}</td>
            <td>{product.noOfClicks}</td>
            <td>{product.orderValue}</td>
            <td>{product.date}</td>
        </tr>
    );


    return (
        <div class="container">
            <div class="row">
                <div class="col">
                    <img class="center-block" src='/blog/img/marketing.jpg' />
                </div>
            </div>
            <div class="row mx-md-n5">
                <div class="col px-md-5">
                    <button class="btn btn-primary" type="button"
                        data-toggle="collapse" data-target="#financereport"
                        aria-expanded="false" aria-controls="financereport">
                        {lsaid} Finance Report
                    </button>
                </div>
                <div class="col px-md-5">
                    <button class="btn btn-primary" type="button"
                        data-toggle="collapse" data-target="#productreport"
                        aria-expanded="false" aria-controls="productreport">
                        {lsaid} Product Promotion Report
                    </button>
                </div>
            </div>
            <div class="collapse" id="productreport">
                <div class="row justify-content-center" >
                    <div class="card card-body">
                        <div id="introduction" class="p-3 mb-2 bg-primary text-white">
                            <h3 class="text-center">All products {lsaid} promote.</h3>
                        </div>
                        {loading &&
                            <div class="spinner-border text-success"></div>
                        }
                        {sucess &&
                            <div class='success-message'>
                                <section id="lsareport">
                                    <div class="table-responsive">
                                        <table class="table">
                                            <thead>
                                                <tr>
                                                    <th scope="col">#</th>
                                                    <th scope="col">Product Id</th>
                                                    <th scope="col">Clicks</th>
                                                    <th scope="col">Sale Units</th>
                                                    <th scope="col">Date</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {productList}
                                            </tbody>
                                        </table>
                                    </div>                                    
                                </section>
                            </div>
                        }
                        {invalidLSA &&
                            <div class='failure-message'>Your LSAID is not valid.</div>
                        }
                    </div>
                </div>
            </div>
            <div class="collapse" id="financereport">
                <div class="row justify-content-center" >
                    <div class="card card-body">
                        <div id="introduction" class="p-3 mb-2 bg-primary text-white">
                            <h3 class="text-center">Local Shop Affliate Finance Report for {lsaid}.</h3>
                        </div>
                        {loading &&
                            <div class="spinner-border text-success"></div>
                        }
                        {sucess &&
                            <div class='success-message'>
                                <section id="lsareport">
                                    <div class="table-responsive">
                                        <table class="table">
                                            <thead>
                                                <tr>
                                                    <th scope="col">#</th>
                                                    <th scope="col">Order Id</th>
                                                    <th scope="col">Product Id</th>
                                                    <th scope="col">Order Value</th>
                                                    <th scope="col">Commission (5%)</th>
                                                    <th scope="col">Date</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {featureList}
                                            </tbody>
                                        </table>
                                    </div>
                                    <div class="p-3 mb-2 bg-primary text-white">
                                        <h4 class="text-center">Total Balance Payable  {payable}</h4>
                                    </div>
                                </section>
                            </div>
                        }
                        {invalidLSA &&
                            <div class='failure-message'>Your LSAID is not valid.</div>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}


