// Dependencies
import React from 'react';

import CatalogSearch from './products'

const Search2 = props => {
    const search = window.location.search;
    const params = new URLSearchParams(search);
    const searchTerm = params.get('searchTerm');
    const isCatalog = true;
    return (
        <div class="row justify-content-around border-bottom">
            <CatalogSearch baseUrl={process.env.REACT_APP_catalogSearchUrl} baseSearchUrl={process.env.REACT_APP_catalogSearchHasMore} searchTerm={searchTerm} isCatalog={isCatalog} />
        </div>
    );
};

export default Search2;