import React, { useReducer, useRef } from 'react';

import { useFetch, useInfiniteScroll, useLazyLoading } from './hooks'
import { Link } from 'react-router-dom';

const Products = (props) => {

    const imgReducer = (state, action) => {
        switch (action.type) {
            case 'STACK_IMAGES':
                return { ...state, images: state.images.concat(action.images) }
            case 'FETCHING_IMAGES':
                return { ...state, fetching: action.fetching }
            default:
                return state;
        }
    }

    const pageReducer = (state, action) => {
        switch (action.type) {
            case 'ADVANCE_PAGE':
                return { ...state, page: state.page + 1 }
            case 'STOP_FETCHING':
                return { ...state, search: 1 }
            default:
                return state;
        }
    }

    const [pager, pagerDispatch] = useReducer(pageReducer, { page: 0, search: 0, category: props.category, subcategory: props.subcategory, productListUrl: props.productListUrl, productListSearchUrl: props.productListSearchUrl })
    const [imgData, imgDispatch] = useReducer(imgReducer, { images: [],  fetching: true, })

    let bottomBoundaryRef = useRef(null);
    useFetch(pager, imgDispatch);
    useLazyLoading('.card-img-top', imgData.images)
    useInfiniteScroll(bottomBoundaryRef, pagerDispatch);

    return (
        <div class="card-columns p-2 ml-5">
            {imgData.fetching && (
                <div class="spinner-border text-warning">loading products...</div>
            )}

            {imgData.images.map((image, index) => {
                const { Title, IMAGE, PID, Price } = image
                const url = '/product/' + PID
                return (
                    <Link to={url} target="_blank">
                        <div key={index} class="card text-center bg-primary w-75">
                            <img
                                alt={Title}
                                data-src={IMAGE}
                                class="card-img-top"
                                src="/img/spinner.gif"
                            />
                            <div class="card-body">
                                <h5 class="card-title text-white">{Title}</h5>
                                <a class="btn btn-primary">{Price}</a>
                            </div>
                        </div>
                    </Link>
                )
            })}
            <div id='page-bottom-boundary' style={{ border: '1px solid red' }} ref={bottomBoundaryRef}></div>
        </div>
    );
}

export default Products;