import { useEffect, useCallback, useRef } from 'react';

// make API calls and pass the returned data via dispatch
export const useFetch = (data, dispatch) => {
    useEffect(async () => {
        var url = data.url + '/' + data.pid;
        await fetch(url)
            .then(data => data.json())
            .then(response => {
            	console.log(response);
            	if(response.status == 'success') {
            		data.product = response.product;
            		data.keyFeatures = response.product.keyFeatures;
            		data.variations = response.product.variations;
            		data.numOfImages = response.product.numOfImages;
            		data.amazonAsin = response.product.marketplace.amazon;
            		data.flipkartPid = response.product.marketplace.flipkart;
                document.title = 'Buy ' + response.product.Title;
                document.querySelector("meta[property='og:image']").content = process.env.REACT_APP_vlocalShopUrl + '/catalog/' + data.pid + '/01-small.jpg';                
                
            		dispatch({ type: 'STACK_IMAGES', response })
	                dispatch({ type: 'FETCHING_PRODUCT', fetching: false }) 
            	}else {
	            	dispatch({ type: 'STACK_IMAGES', response })
	                dispatch({ type: 'FETCHING_PRODUCT', fetching: false })      
            	}
            })
            .catch(e => {
                // handle error
                dispatch({ type: 'FETCHING_PRODUCT', fetching: false })
                return e;
            })
    }, [dispatch, data.page])
}

// lazy load images with intersection observer
export const useLazyLoading = (imgSelector, items) => {
  const imgObserver = useCallback(node => {	 
    const intObs = new IntersectionObserver(entries => {    	 
      entries.forEach(en => {
        if (en.intersectionRatio > 0) {
          const currentImg = en.target;
          const newImgSrc = currentImg.dataset.src;
          // only swap out the image source if the new url exists
          if (!newImgSrc) {
            console.error('Image source is invalid');
          } else {
            currentImg.src = newImgSrc;
          }
          intObs.unobserve(node);
        }
      });
    })
    intObs.observe(node);
  }, []);

  const imagesRef = useRef(null);

  useEffect(() => {
    imagesRef.current = document.querySelectorAll(imgSelector);

    if (imagesRef.current) {
      imagesRef.current.forEach(img => imgObserver(img));
    }
  }, [imgObserver, imagesRef, imgSelector, items])
}