import React from 'react';
import Products from './products'

const Category = props => {

    return (
        <div class="row justify-content-around border-bottom">
            <Products category={props.match.params.id} productListUrl={process.env.REACT_APP_categoryUrl} productListSearchUrl={process.env.REACT_APP_categoryUrl}/>
        </div>
    );
}

export default Category;