import React, { useState } from 'react';
import { Redirect } from "react-router-dom";

export const Form = ({ closeModal, props }) => {
    const [phoneError, setphoneError] = useState(0);
    const [noError, setNoError] = useState('1234567891');
    const [showLoading, setShowLoading] = useState('');
    const [redirect, setRedirect] = useState(false);
    const [toUrl, setToUrl] = useState('/order/');

    const onSubmit = (event) => {
        event.preventDefault(event);
        const phoneNo = event.target.phone.value;
        if (phoneNo.length < 10) {
            setphoneError('Phone must be at least 10 digits long!')
        } else {
            const pid = event.target.pid.value;
            const title = event.target.title.value;
            const price = event.target.price.value;
            //const order = { phoneNo: phoneNo, productId: pid, productTitle: title, price: price }
            const url = process.env.REACT_APP_orderUrl + '?pid=' + pid + '&phoneNo=' + phoneNo + '&title=' + title + '&price=' + price
            setShowLoading('true');
            setphoneError('error')
            console.log('making remote call ' + url);
            fetch(url)
                .then(data => data.json())
                .then(response => {

                    if (response.status === 'sucess') {
                        setToUrl('/order/' + response.orderId + '/' + response.phoneNo);

                        setRedirect(true);
                    } else {
                        setShowLoading('false');
                    }
                })
                .catch(e => {
                    // handle error
                    throw e;
                })


        }
    };

    const handleChange = (event) => {
        event.preventDefault();
        const { phone, value } = event.target;
        if (value.length < 10) {
            setphoneError('Phone must be at least 10 digits long!')
        } else {
            setphoneError('')
            setNoError('error');
        }

        var phoneno = /^\d{10}$/;
        if (!value.match(phoneno)) {
            setphoneError('Phone must be at least 10 digits long!')
            setNoError('error');
        } else {
            setphoneError('')
            setNoError('success...');
        }

    }
    const price = '₹ ' + props.productPrice;
    return (

        <form onSubmit={onSubmit}>
            {showLoading.length == 0 &&
                <div>
                    <div className="form-group">
                        <label className="form-label" htmlFor="productId">ProductID </label>
                        <input readOnly value={props.productId} id="pid" />
                    </div>
                    <div className="form-group">
                        <label className="form-label" htmlFor="productId">ProductTitle </label>
                        <input readOnly value={props.productTitle} id="title" />
                    </div>
                    <div className="form-group">
                        <img width="200px" height="200px" src={props.productImg} />
                    </div>
                    <div className="form-group">
                        <label className="form-label" htmlFor="payable">Cash On Delivery </label>
                        <input readOnly value={price} id="price" />
                    </div>
                    <div className="form-group">
                        <label className="form-label" htmlFor="phone">Enter Your Phone No Below to Submit</label>
                        <input className="form-control" id="phone" onChange={handleChange} />
                        {phoneError.length > 0 &&
                            <span className='error'>{phoneError}</span>}
                    </div>
                    {noError.length == 10 &&
                        <button className="form-control btn btn-primary" type="submit">
                            Submit
    	                </button>
                    }
                </div>
            }



            {showLoading.length > 0 &&
                <div className="form-group">
                    <img src={process.env.PUBLIC_URL + '/img/waiting.gif'} />
                </div>
            }

            {redirect == true &&
                <Redirect to={toUrl} />
            }
        </form>
    );
};
export default Form;
