import React, { Component } from 'react'
import './index.css';

const Frame = (props) => {

    return (
        <div className="frame-container">
            <div className="market-comparision">
                <h3> Compare Market Place </h3>
            </div>
            <div className="market-place">
                <div className="amazon-container">
                    <a href={props.amazonLink} target="_blank">
                        <div className="amazon-product">
                            <img src='/img/amazon-small.jpg' alt="Amazon"></img>
                            <img src={props.srcImgSmall} alt="Amazon"></img>
                            <p>{props.title}</p>
                            <p>{props.amazonPrice}</p>
                        </div>
                    </a>
                </div>
				<div class="amazon-container">
					<a href='https://www.vlocalshop.in/product/Y0C38CGPRA' target="_blank">
						<div class="amazon-product">
							<img src='/img/logo100x.jpg' alt="vLocalShop"></img>
							<img src={props.srcImgSmall} alt="LocalShop"></img>
							<p>{props.title}</p>
							<p>{props.vlocalShopPrice}</p>
						</div>
					</a>
				</div>
                <div className="amazon-container">
                    <a href={props.flipkartLink} target="_blank">
                        <div className="amazon-product">
                            <img src='/img/flipkart-small.jpg' alt="Flipkart"></img>
                            <img src={props.srcImgSmall} alt="Flipkart"></img>
                            <p>{props.title}</p>
                            <p>{props.flipkartPrice}</p>
                        </div>
                    </a>
                </div>
            </div>

        </div>
    );

}

export default Frame