import React, { Component } from 'react';

import Header2 from './header';
import Footer2 from './footer';
import './index.css';

export default class AppLayout2 extends Component {

    constructor(props) {
        super(props)
        this.state = {
            showSideBar: false
        }
    }

    render() {
        return (
            <React.Fragment>
                <div class="container-fluid">
                    <Header2 />
                </div>
            </React.Fragment>
        );
    }
}