import React from "react";

class OrderConfirmation extends React.Component {
	  constructor (props) {
		    super(props)
	}

    render() {
        return (
            <div style={{ height: 200 }} id="myCarousel" class="carousel slide" data-ride="carousel">               
            	Your Order &nbsp;
            	<label className="form-label" htmlFor="oid"> {this.props.match.params.orderId} </label>
            	&nbsp;is confirmed we will get back to you shortly on your phone no  &nbsp;   
            	  	<label className="form-label" htmlFor="phone">   {this.props.match.params.phone}&nbsp; </label>. 
            </div>
        )
    };
}

export default OrderConfirmation;