import React from "react";

export const SocialMedia = (props) => {
	const facebookLink = process.env.REACT_APP_LS_FACEBOOK_LINK + props.productLink + '&redirect_uri=' + props.productLink;
	const twitterLink = process.env.REACT_APP_LS_TWITTER_LINK + props.productLink;
	const pinLink = process.env.REACT_APP_LS_PINT_LING + props.productLink;
	const whatsapplink = props.whatsapplink;
	const mail = 'mailto:?subject=Check this out at LocalShop&body=I want to recommend this product at Local Shop, Check out this product ' + props.productLink;
	var socialMediaColors = {
		"facebook": "#3B5998",
		"twitter": "#55ACEE",
		"pinterest": "#cb2027",
		"instagram": "#125688",
		"linkedin": "#007bb5",
		"youtube": "#bb0000",
		"whatsapp": "#0fec2d",
	};
	return (
		<div>
			<div class="w-50 p-3 mb-2 bg-primary text-white">
				<p class="h5 text-center">Share to Your Friends and Family</p>
			</div>
			<a href={facebookLink}>
				<span style={{ color: socialMediaColors.facebook }}>
					<i class="fab fa-3x fa-facebook-square"></i>&nbsp;&nbsp;
				</span>
			</a>
			<a href={twitterLink}>
				<span style={{ color: socialMediaColors.twitter }}>
					<i class="fab fa-3x fa-twitter-square"></i>&nbsp;&nbsp;
                </span>
			</a>
			<a href={pinLink}>
				<span style={{ color: socialMediaColors.pinterest }}>
					<i class="fab fa-3x fa-pinterest-square"></i>&nbsp;&nbsp;
                </span>
			</a>			
			<a href={whatsapplink} target="_blank">
				<span style={{ color: socialMediaColors.whatsapp }}>
					<i class="fab fa-3x fa-whatsapp-square"></i>&nbsp;&nbsp;
                </span>
			</a>
			<a href={mail} title="Share by Email" target="_blank">
				<span style={{ color: socialMediaColors.linkedin }}>
					<i class="fas fa-3x fa-envelope-square"></i>
				</span>
			</a>
		</div>
	);
}
export default SocialMedia;