import React, { useState } from "react";

export default function AffliateRegister(props) {
    const [values, setValues] = useState({
        name: '',
        phone: '',
        email: '',
    });

    const [submitted, setSubmitted] = useState(false);
    const [showForm, setShowForm] = useState(true);
    const [validPhone, setValidPhone] = useState(true);
    const [success, setSuccess] = useState(false);

    const handleSubmit = (evt) => {
        evt.preventDefault();
        setSubmitted(true);
        setShowForm(false);
        setSuccess(true);
    }

    const handleNameChange = (event) => {
        event.persist();
        setValues((values) => ({
            ...values,
            name: event.target.value,
        }));
    }
    const handleEmailChange = (event) => {
        event.persist();
        setValues((values) => ({
            ...values,
            email: event.target.value,
        }));
    };
    const handlePhoneChange = (event) => {
        event.persist();
        var phoneno = /^\d{10}$/;
        if (!event.target.value.match(phoneno)) {
            setValidPhone(false)
        }
        setValues((values) => ({
            ...values,
            phone: event.target.value,
        }));
    };

    return (
        <div class="container">
            <div class="row justify-content-center">
                <div >
                    <div >
                        <img src='/blog/img/marketing.jpg' />
                    </div>
                    <div id="introduction" class="p-3 mb-2 bg-primary text-white">
                        <h3 class="text-center">Local Shop Affliate Registration.</h3>
                    </div>
                    {showForm &&
                        <form onSubmit={handleSubmit}>
                            <div class="form-group">
                                <label class="col-sm-2 col-form-label" for="name">Your Name</label>
                                <input type="text" class="form-control" name="name" id="name"
                                    aria-describedby="emailHelp" placeholder="Enter Name"
                                    value={values.name} onChange={handleNameChange}
                                    pattern="[A-Za-z0-9]+" required />
                            </div>
                            <div class="form-group">
                                <label class="col-sm-2 col-form-label" for="phone">Your Phone</label>
                                <input type="tel" class="form-control" name="phone" id="phone"
                                    aria-describedby="emailHelp" placeholder="Enter Your Phone"
                                    value={values.phone} onChange={handlePhoneChange}
                                    pattern="[7-9]{1}[0-9]{9}" required />
                            </div>
                            <div class="form-group">
                                <label class="col-sm-2 col-form-label" for="email">Email address</label>
                                <input type="email" class="form-control" name="email" id="email"
                                    aria-describedby="emailHelp" placeholder="Enter email"
                                    value={values.email} onChange={handleEmailChange} required />
                                <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone
                    else.</small>
                            </div>
                            <div class="form-check">
                                <input type="checkbox" class="form-check-input" id="exampleCheck1" />
                                <label class="form-check-label" for="exampleCheck1">Check me out</label>
                            </div>
                            <button className="form-control btn btn-primary" type="submit">
                                Register
    	            </button>
                        </form>
                    }

                    {success &&
                        <div class='success-message'>Success! Thank you for registering</div>
                    }
                    {submitted && !success &&
                        <div class='failure-message'>Failed! Please try again</div>
                    }
                </div>

            </div>
        </div>
    );
}
