import React from 'react';
import Products from './products'

const SubCategory = props => {

    return (
        <div class="row justify-content-around border-bottom">
            <Products category={props.match.params.id} subcategory={props.match.params.sid} productListUrl={process.env.REACT_APP_categoryUrl} productListSearchUrl={process.env.REACT_APP_categoryUrl} />
        </div>
    );
}

export default SubCategory;