import React from 'react';
import ReactDOM from 'react-dom';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import AppLayout2 from './components/layout2';
import App2 from './components/app/v2';
import Product from './components/product3';
import Category2 from './components/category/v2';
import SubCategory2 from './components/category/v2/subcategory';
import OrderConfirmation from './components/order';
import CatalogSearch2 from './components/search/v2';
import AffliateRegister from './components/affliate/register';
import AffliateInfo from './components/affliate/id';

ReactDOM.render(
  <BrowserRouter>
    <AppLayout2>
      <Switch>
        <Route exact path="/" component={App2} />
        <Route exact path="/product/:id" component={Product} />
        <Route exact path="/category/:id" component={Category2} />
        <Route exact path="/category/:id/:sid" component={SubCategory2} />
        <Route exact path="/order/:orderId/:phone" component={OrderConfirmation} />
        <Route exact path="/catalog/search/" component={CatalogSearch2} />
        <Route exact path="/affliate/register" component={AffliateRegister} />
        <Route exact path="/affliate/report/:id" component={AffliateInfo} />
      </Switch>
    </AppLayout2>
  </BrowserRouter>

  , document.getElementById('root'));
registerServiceWorker();


